.tm-paging-links {
    text-align: center;
    margin-bottom: 40px;
}

.tm-paging-item {
    list-style: none;
    display: inline-block;
    border: 1px solid #2F956D;
    margin: 7px;
}

.tm-paging-link {
    padding: 10px 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-decoration: none;
    color: #2F956D;
    background-color: transparent;
}

.tm-paging-link.active,
.tm-paging-link:hover {
    background-color: #2F956D;
    color: white;
}



